import { queryPositionPage, queryHistoryCloseOrderList, queryPBOOrderPage } from '@/api/trade'
import CheckAPI from '@/utils/checkAPI'

const EmptyProfitLossRang = {
    buyProfitRange: [], // 
    sellProfitRange: [], // 
    buyStopLossRange: [], // 
    sellStopLossRange: [], // 
}
const EmptyPendingPriceRang = {
    buyLimitRange: [], // 
    sellLimitRange: [], // 
    buyStopRange: [], // 
    sellStopRange: [], // 
}

export default {
    namespaced: true,
    state: {
        modifyPositionId: 0, // ID
        pendingPrice: 0, // 
        positionLoading: '', // 
        positionList: [], // 
        positionMap: [], // map
        historyLoading: false, // 
        historyList: [], // 
        pendingList: [], // 
        pendingMap: [], // 
        positionProfitLossList: [] // 
    },
    getters: {
        // 
        product (state, getters, rootState) {
            const productID = rootState._quote.productActivedID
            const product = rootState._quote.productMap[productID]
            return product
        },
        // 
        marketProfitLossRang (state, getters, rootState) {
            const product = getters.product
            if (!product) return EmptyProfitLossRang
            const curPosition = state.positionMap[state.modifyPositionId] // 
            const digits = product.price_digits
            const point = Math.pow(0.1, digits)
            const pip = point * product.pointRatio
            let buyProfitMax, // 
                buyProfitMin, // 
                sellProfitMax, // 
                sellProfitMin, // 
                buyStopLossMax, // 
                buyStopLossMin, // 
                sellStopLossMax, // 
                sellStopLossMin // 

            let buy_price = state.pendingPrice ? Number(state.pendingPrice) : Number(product.buy_price)
            let sell_price = state.pendingPrice ? Number(state.pendingPrice) : Number(product.sell_price)
            if (curPosition) {
                // 
                buy_price = sell_price = curPosition.direction === 1 ? Number(product.sell_price) : Number(product.buy_price)
            }

            buyProfitMax = (buy_price + pip * product.stopLossMaxPoint).toFixed(digits) // +pip*
            buyProfitMin = (buy_price + pip * product.stopLossMinPoint).toFixed(digits) // +pip*

            sellProfitMax = (sell_price - pip * product.stopLossMinPoint).toFixed(digits) // -pip*

            sellProfitMin = (sell_price - pip * product.stopLossMaxPoint).toFixed(digits) // -pip*

            buyStopLossMax = (buy_price - pip * product.stopLossMinPoint).toFixed(digits) // -pip*
            buyStopLossMin = (buy_price - pip * product.stopLossMaxPoint).toFixed(digits) // -pip*

            sellStopLossMax = (sell_price + pip * product.stopLossMaxPoint).toFixed(digits) // +pip*
            sellStopLossMin = (sell_price + pip * product.stopLossMinPoint).toFixed(digits) // +pip*

            return {
                buyProfitRange: [buyProfitMin, buyProfitMax], // 
                sellProfitRange: [sellProfitMin, sellProfitMax], // 
                buyStopLossRange: [buyStopLossMin, buyStopLossMax], // 
                sellStopLossRange: [sellStopLossMin, sellStopLossMax], // 
            }
        },
        // 
        pendingPriceRang (state, getters, rootState) {
            const product = getters.product
            if (!product || state.pendingPrice === 0) return EmptyPendingPriceRang
            const digits = product.price_digits
            const point = Math.pow(0.1, digits)
            const pip = point * product.pointRatio
            let buyLimitMax, // 
                buyLimitMin, // 
                sellLimitMax, // 
                sellLimitMin, // 
                buyStopMax, // 
                buyStopMin, // 
                sellStopMax, // 
                sellStopMin // 

            const buy_price = Number(product.buy_price)
            const sell_price = Number(product.sell_price)

            buyLimitMax = (buy_price - pip * product.priceMinLimit).toFixed(digits) // -pip*
            buyLimitMin = (buy_price - pip * product.priceMaxLimit).toFixed(digits) // -pip*

            sellLimitMax = (sell_price + pip * product.priceMaxLimit).toFixed(digits) // +pip*
            sellLimitMin = (sell_price + pip * product.priceMinLimit).toFixed(digits) // +pip*

            buyStopMax = (buy_price + pip * product.priceMaxLimit).toFixed(digits) // +pip*
            buyStopMin = (buy_price + pip * product.priceMinLimit).toFixed(digits) // +pip*

            sellStopMax = (sell_price - pip * product.priceMinLimit).toFixed(digits) // -pip*
            sellStopMin = (sell_price - pip * product.priceMaxLimit).toFixed(digits) // -pip*

            return {
                buyLimitRange: [buyLimitMin, buyLimitMax], // 
                sellLimitRange: [sellLimitMin, sellLimitMax], // 
                buyStopRange: [buyStopMin, buyStopMax], // 
                sellStopRange: [sellStopMin, sellStopMax], // 
            }
        }
    },
    mutations: {
        Empty_data (state, data) { // 
            state.positionList = []
            state.positionMap = {}
            state.historyList = []
            state.pendingList = []
            state.pendingMap = {}
            state.positionProfitLossList = []
        },
        Update_modifyPositionId (state, data) {
            state.modifyPositionId = data
        },
        Update_pendingPrice (state, data) {
            state.pendingPrice = data
        },
        Update_positionLoading (state, data) {
            state.positionLoading = data
        },
        Update_positionList (state, data) {
            state.positionList = data
            const positionMap = state.positionMap
            data.forEach(item => {
                if (!item || !item.positionId) return false
                const curPosition = positionMap[item.positionId]
                if (curPosition) Object.assign(item, { profitLoss: curPosition.profitLoss }) // mapitem
                positionMap[item.positionId] = item
            })
        },
        Update_historyLoading (state, data) {
            state.historyLoading = data
        },
        Update_historyList (state, data) {
            state.historyList = data
        },
        Update_pendingList (state, data) {
            state.pendingList = data
            const pendingMap = state.pendingMap
            data.forEach(item => {
                if (!item || !item.id) return false
                if (!pendingMap[item.id]) pendingMap[item.id] = {}
                Object.assign(pendingMap[item.id], item)
            })
        },
        Update_positionProfitLossList (state, dataList = []) {
            state.positionProfitLossList = dataList
            const positionMap = state.positionMap
            dataList.forEach(({ positionId, profitLoss }) => {
                if (positionMap[positionId]) positionMap[positionId].profitLoss = profitLoss
                else positionMap[positionId] = { profitLoss }
            })
        },
    },
    actions: {
        // 
        queryPositionPage ({ dispatch, commit, state, rootState }, params = {}) {
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            dispatch('queryPBOOrderPage')

            if (!accountListLen) {
                commit('Update_positionList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            commit('Update_positionLoading', true)
            return queryPositionPage(params).then((res) => {
                commit('Update_positionLoading', false)
                if (res.check()) {
                    commit('Update_positionList', res.data)
                }
                return res
            })
        },
        // 
        queryHistoryCloseOrderList ({ dispatch, commit, state, rootState }, params = {}) {
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            if (!accountListLen) {
                commit('Update_historyList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            commit('Update_historyLoading', true)
            return queryHistoryCloseOrderList(params).then((res) => {
                commit('Update_historyLoading', false)
                if (res.check()) {
                    const newList = params.current === 1 ? res.data.list : state.historyList.concat(res.data.list)
                    commit('Update_historyList', newList)
                }
                return res
            })
        },
        // 
        queryPBOOrderPage ({ dispatch, commit, state, rootState }, params = {}) {
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            if (!accountListLen) {
                commit('Update_pendingList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            return queryPBOOrderPage(params).then((res) => {
                if (res.check()) {
                    commit('Update_pendingList', res.data)
                }
                return res
            })
        },
    }
}
