import request from '@/utils/request'
import { getDevice } from '@/utils/util'

/*  */
export function login (data) {
    return request({
        url: '/login/customer.app.CustomerWebApiService.login',
        method: 'post',
        // toastErr: false,
        headers: {
            version: '0.0.1',
        },
        data: {
            device: getDevice(),
            ...data,
        }
    })
}

/*  */
export function logout (data) {
    return request({
        url: '/logout',
        method: 'post',
        headers: {
            version: '0.0.1',
        },
        data
    })
}

