<template>
    <div class='m-file'>
        <el-form-item :label='config.label'>
            <el-input placeholder='' :value='activeData' @input='input'>
                <el-button
                    slot='append'
                    icon='el-icon-upload'
                    type='primary'
                    @click='uploadFile'
                >
                    
                </el-button>
            </el-input>
        </el-form-item>
    </div>
</template>

<script>
export default {
    name: 'File',
    props: {
        config: {
            type: Object,
            default () {
                return {}
            }
        },
        activeData: {
            type: String,
        }
    },
    data () {
        return {
        }
    },
    mounted () {
        // wpcallback

    },
    methods: {
        uploadFile () {
            // wp
            if (window.tb_show) {
                tb_show('', 'media-upload.php?type=image&amp;TB_iframe=true')
                // callBack
                window.send_to_editor = (html) => {
                    if (window.tb_remove) {
                        tb_remove()
                    }
                    console.log(html)
                    const _div = document.createElement('div')
                    _div.innerHTML = html
                    const imgUrl = _div.querySelector('img').src
                    this.$emit('formChange', imgUrl)
                }
            }
        },
        input (val) {
            this.$emit('formChange', val)
        }
    }
}
</script>

