<template>
    <div class='rightForm'>
        <div
            v-for='(config, index) in elementConfig'
            :key='config.type + config.name + index'
        >
            <template
                v-if='
                    !config.hidden &&
                        !(
                            (config.exclude &&
                                config.exclude.includes(
                                    activeData[config.filterName]
                                )) ||
                            (config.include &&
                                !config.include.includes(
                                    activeData[config.filterName]
                                ))
                        )
                '
            >
                <el-form-item
                    v-if="config.type == 'Input' || config.type == 'Textarea'"
                    :label='config.label'
                    :row='8'
                >
                    <el-input
                        v-model='activeData[config.name]'
                        placeholder=''
                        :type='config.type.toLowerCase()'
                    />
                </el-form-item>
                <el-form-item
                    v-else-if="config.type == 'Select'"
                    :label='config.label'
                >
                    <el-select
                        v-model='activeData[config.name]'
                        clearable
                        placeholder=''
                    >
                        <el-option
                            v-for='item in config.options'
                            :key='item.value'
                            :label='item.label'
                            :value='item.value'
                        />
                    </el-select>
                </el-form-item>
                <el-form-item
                    v-else-if="config.type == 'Radio'"
                    :label='config.label'
                >
                    <el-radio-group
                        v-model='activeData[config.name]'
                        placeholder=''
                    >
                        <el-radio
                            v-for='item in config.options'
                            :key='item.value'
                            :label='item.value'
                        >
                            {{ item.label }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item
                    v-else-if="config.type == 'Tinymce'"
                    :label='config.label'
                >
                    <Tinymce v-model='activeData[config.name]' :height='300' />
                </el-form-item>
                <el-form-item
                    v-else-if="config.type === 'Checkbox'"
                    :label='config.label'
                >
                    <el-checkbox-group
                        v-model='activeData[config.name]'
                        placeholder=''
                    >
                        <el-checkbox
                            v-for='item in config.options'
                            :key='item.value'
                            :label='item.value'
                        >
                            {{ item.label }}
                        </el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item
                    v-else-if="config.type === 'DatePicker'"
                    :label='config.label'
                >
                    <el-date-picker
                        v-model='activeData[config.name]'
                        end-placeholder=''
                        format='YYYY-MM-DD HH:mm:ss'
                        range-separator=''
                        start-placeholder=''
                        type='datetimerange'
                        :unlink-panels='true'
                    />
                </el-form-item>
                <Href
                    v-else-if="config.type === 'Href'"
                    :active-data='activeData[config.name]'
                    :config='config'
                    @formChange='
                        (formData) => {
                            updateFormData(config.name, formData);
                        }
                    '
                />
                <File
                    v-else-if="config.type === 'File'"
                    :active-data='activeData[config.name]'
                    :config='config'
                    @formChange='
                        (formData) => {
                            updateFormData(config.name, formData);
                        }
                    '
                />
                <Product
                    v-else-if="config.type === 'Product'"
                    :active-data='activeData[config.name]'
                    :config='config'
                    @formChange='
                        (formData) => {
                            updateFormData(config.name, formData);
                        }
                    '
                />
                <div v-if="config.type == 'Object'">
                    <el-divider v-if='config.label' content-position='left'>
                        {{
                            config.label
                        }}
                    </el-divider>
                    <right-form
                        :active-data='activeData[config.name]'
                        :element-config='config.formConfig'
                    />
                </div>
                <div v-if="config.type == 'Array'">
                    <draggable
                        v-model='config.formConfig'
                        :animation='340'
                        handle='.handle'
                        :item-key='config.id||config._id'
                        @end='
                            (evt) =>
                                onEnd({
                                    oldIndex: evt.oldIndex,
                                    newIndex: evt.newIndex,
                                    key: config.name,
                                })
                        '
                    >
                        <template #item='{ element:ArrayItem, index: ArrayIndex }'>
                            <div
                                class='list-item'
                                :class="{ 'odd':(ArrayIndex%2==0) }"
                            >
                                <el-divider
                                    class='handle'
                                    content-position='left'
                                >
                                    {{ config.label }}
                                    <el-button
                                        v-if='config.formConfig.length > 1'
                                        class='delete-btn'
                                        icon='el-icon-delete'
                                        type='text'
                                        @click='
                                            deleteRow(
                                                config.formConfig,
                                                activeData[config.name],
                                                ArrayIndex
                                            )
                                        '
                                    >
                                        
                                    </el-button>
                                </el-divider>
                                <right-form
                                    :active-data='
                                        activeData[config.name][ArrayIndex]
                                    '
                                    :element-config='ArrayItem'
                                />
                            </div>
                        </template>
                    </draggable>
                    <el-row>
                        <el-col :span='24' style='text-align: center'>
                            <el-button
                                v-if='config.formConfig.length < config.max'
                                icon='el-icon-plus'
                                type='text'
                                @click='
                                    addRow(
                                        config.formConfig,
                                        activeData[config.name]
                                    )
                                '
                            >
                                {{ config.label }}
                            </el-button>
                        </el-col>
                    </el-row>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
import Tinymce from '@index/components/Tinymce'
import draggable from 'vuedraggable'
import Href from './Href'
import File from './File'
import Register from './Register'
import Product from './Product'
export default {
    name: 'RightForm',
    components: {
        Tinymce,
        draggable,
        Href,
        File,
        Register,
        Product
    },
    props: {
        elementConfig: {
            type: Array,
            default () {
                return []
            }
        },
        activeData: {
            type: Object,
            default () {
                return {}
            }
        },
    },
    data () {
        return {
        }
    },
    methods: {
        addRow (formConfig, data) {
            this.$store.commit('editor/ADD_FROM_ROW', {
                formConfig: formConfig,
                data: data
            })
        },
        deleteRow (formConfig, data, index) {
            this.$store.commit('editor/DELETE_FROM_ROW', { formConfig: formConfig, data: data, index })
        },
        onEnd (evt) {
            // 
            console.log(evt)
            this.$store.commit('editor/CHANGE_INDEX_FROM_ROW', evt)
        },
        updateFormData (key, data) {
            this.activeData[key] = data
            // this.$store.commit('editor/UPDATE_FORM_DATA', {key:key,value:data})
        }
    }
}
</script>

<style lang="scss" scoped>
.delete-btn {
    margin-left: 15px;
}
.rightForm {
    padding-bottom: 10px;
}
 .odd {
    background-color: #fafafa;
  }

</style>
