<template>
    <div class='m-register'>
        {{ activeData }}
        <el-form-item label=''>
            <el-select v-model='activeData.name' clearable placeholder=''>
                <el-option
                    v-for='item in nameList'
                    :key='item.value'
                    :label='item.label'
                    :value='item.value'
                />
            </el-select>
        </el-form-item>
        <el-form-item label='label'>
            <el-input
                v-model='activeData.label'
                placeholder=''
            />
        </el-form-item>
        <el-form-item v-if="activeData.name === 'code'" label=''>
            <el-select v-model='activeData.bindName' clearable placeholder=''>
                <el-option
                    v-for='item in bindList'
                    :key='item.value'
                    :label='item.label'
                    :value='item.value'
                />
            </el-select>
        </el-form-item>
        <template v-if="activeData.name !=='submit'">
            <el-form-item label='placeholder'>
                <el-input
                    v-model='activeData.placeholder'
                    placeholder=''
                />
            </el-form-item>
            <el-form-item label=''>
                <el-input
                    v-model='activeData.validator'
                    placeholder=''
                    type='textarea'
                />
            </el-form-item>
        </template>
    </div>
</template>

<script>
export default {
    name: 'Register',
    props: {
        config: {
            type: Object,
            default () {
                return {}
            }
        },
        activeData: {
            type: Object,
        }
    },
    data () {
        return {
            nameList: [
                { value: 'phone', label: '' },
                { value: 'email', label: '' },
                { value: 'code', label: '' },
                { value: 'pwd', label: '' },
                { value: 'submit', label: '' }
            ],
            bindList: [
                { value: 'phone', label: '' },
                { value: 'email', label: '' }
            ]
        }
    },
    mounted () {

    },
    methods: {

    }
}
</script>

